import React, { Component } from "react";
import HandleText from "./HandleText";
import Shuffle from "./Shuffle";
import "./Container.css";
import "./Button.css";
import "./App.css";
class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      english_text: "",
      vietnames_text: "",
      final_text: [],
      guessWord: "",
      checkAnswer: "",
      createAnswer: "",
      key: 0,
      index: 0,
      storeIndex: "",
      countGuess: 0,
      bool: false,
      english: "",
      vietnames: "",
      submit: true,
      value: "default",
      voice: "US English Male",
      valueInput: "",
      inputArr: [],
    };
    this.handleGuessButtons = this.handleGuessButtons.bind(this);
    this.handleGuessBox = this.handleGuessBox.bind(this);
    this.handleOtherButton = this.handleOtherButton.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.showAnswers = this.showAnswers.bind(this);
    this.handleSelectChange = this.handleSelectChange.bind(this);
  }

  componentDidMount() {
    const script = document.createElement("script");

    script.src =
      "https://code.responsivevoice.org/responsivevoice.js?key=FdquDWj4";
    script.async = true;

    document.body.appendChild(script);
  }

  handleGuessBox(key) {
    // && this.state.guessWord.length > 0
    if (key !== this.state.key) {
      this.setState((str) => ({
        //guessWord: "",
        key: key,
        index: 0,
        countGuess: 0,
      }));
    }
  }
  handleOtherButton(evt) {
    let ltr = evt.target.value;
    if (ltr !== "___") {
      //var msg = new SpeechSynthesisUtterance(ltr);
      //window.speechSynthesis.speak(msg);
      window.responsiveVoice.speak(ltr, this.state.voice);
    }
  }
  handleGuessButtons(evt) {
    let ltr = evt.target.value;
    const key = this.state.key;
    const storeIndex = this.state.storeIndex;
    const k = storeIndex[key];
    let countGuess = this.state.countGuess;
    if (countGuess < 1) {
      //var msg = new SpeechSynthesisUtterance(ltr);
      // eslint-disable-next-line no-unused-expressions
      window.responsiveVoice.speak(ltr, this.state.voice);
      //window.speechSynthesis.speak(msg);
      // eslint-disable-next-line no-unused-expressions
    }

    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    if (this.state.checkAnswer[key][k] === ltr) {
      let newAnswer = this.state.createAnswer.slice(
        0,
        this.state.createAnswer.length
      );
      if (countGuess === 1) {
        //msg = new SpeechSynthesisUtterance(ltr);
        //window.speechSynthesis.speak(msg);
        window.responsiveVoice.speak(ltr, this.state.voice);
      }
      final_text[key].splice(final_text[key].indexOf(ltr), 1);
      // final_text[this.state.key][final_text[this.state.key].indexOf(ltr)] =
      //   "__";
      if (countGuess !== 1 && this.state.bool) {
        //msg = new SpeechSynthesisUtterance(ltr);

        //window.speechSynthesis.speak(msg);
        window.responsiveVoice.speak(ltr, this.state.voice);
      }
      newAnswer[key][k] = ltr;
      let copy = storeIndex.slice(0, storeIndex.length);
      copy[key] = k + 1;
      this.setState((str) => ({
        createAnswer: newAnswer,
        index: k + 1,
        guessWord: ltr,
        final_text: final_text,
        storeIndex: copy,
        countGuess: 0,
        bool: false,
      }));
    } else {
      countGuess = countGuess + 1;
      if (countGuess >= 2) {
        //countGuess = 0;

        let text = `The next word is ${this.state.checkAnswer[key][k]}`;

        //window.speechSynthesis.speak(text);
        window.responsiveVoice.speak(text, this.state.voice);
        // alert(
        //   "Next Word : " +
        //     this.state.checkAnswer[this.state.key][
        //       this.state.storeIndex[this.state.key]
        //     ]
        // );
      }
      this.setState((str) => ({
        guessWord: ltr,
        countGuess: countGuess,
        bool: true,
      }));
    }
  }

  handleReadText(key) {
    if (this.state.final_text[key].length === 0) {
      window.responsiveVoice.speak(
        this.state.english_text[key],
        this.state.voice
      );
      // let msg = new SpeechSynthesisUtterance(this.state.english_text[key]);
      // msg.rate = 0.8;
      // //window.speechSynthesis.speak(msg);
    }
  }
  generateButtons() {
    return this.state.final_text.map((elements, i) => {
      return (
        <section
          className="Container"
          key={i}
          value={i}
          onMouseEnter={() => this.handleGuessBox(i)}
        >
          {this.state.createAnswer[i].map((element, index) => (
            <button
              className="Button"
              key={index}
              value={element}
              onClick={this.handleOtherButton}
            >
              {element}
            </button>
          ))}
          <h1>{this.state.vietnames_text[i]}</h1>
          {/* <h1>{this.state.english_text[i]}</h1> */}
          {/* <h1>{elements.join(" ")}</h1> */}
          {elements.map((element, index) => (
            <button
              className="Button"
              key={index}
              value={element}
              onClick={this.handleGuessButtons}
            >
              {element}
            </button>
          ))}
          {this.state.inputArr[i] && this.state.final_text[i].length === 0 ? (
            <input
              type="text"
              name="valueInput"
              placeholder="Typing Answer"
              value={this.state.valueInput}
              onChange={(evt) => this.handleChange(evt, i)}
              id="typingAnswer"
            />
          ) : (
            <p></p>
          )}
          {this.state.final_text[i].length === 0 ? (
            <div>
              <audio
                src="https://media.vocaroo.com/mp3/9EciEJW3Cyt"
                autoPlay
                controls
                hidden
              ></audio>
              <h1 id="meo">{this.state.english_text[i]}</h1>

              <button
                className="Button"
                key={i}
                onClick={() => this.handleReadText(i)}
              >
                Read Text
              </button>
            </div>
          ) : (
            <div id="showanswer">
              <button
                className="Button"
                style={{
                  background: "#2c3e50",
                  border: "none",
                  color: "#e2e5e9",
                }}
                key={i}
                onClick={() => this.showAnswers(i)}
              >
                Show Answer
              </button>
            </div>
          )}
        </section>
      );
    });
  }

  //handleUpdate() {}
  handleChange(evt, key) {
    let index = this.state.index;
    let checkAnswer = this.state.checkAnswer;
    let inputArr = this.state.inputArr;
    let copy = inputArr.slice(0, inputArr.length);
    this.setState(
      {
        [evt.target.name]: evt.target.value,
      },
      function () {
        if (checkAnswer.length > 0) {
          if (this.state.valueInput === "meowmeow") {
            window.responsiveVoice.speak("You said meowmeow", this.state.voice);
            this.setState({
              valueInput: "",
              index: index + 1,
            });
          }
          if (
            this.state.valueInput.toLowerCase() ===
            checkAnswer[key][index].toLowerCase()
          ) {
            window.responsiveVoice.speak(
              this.state.valueInput,
              this.state.voice
            );
            if (index === checkAnswer[key].length - 1) {
              var audio = new Audio(
                "https://media.vocaroo.com/mp3/9EciEJW3Cyt"
              );
              audio.play();
              console.log("Yes");
              copy[key] = false;
            }

            this.setState({
              valueInput: "",
              index: index + 1,
              inputArr: copy,
            });
          }
        }
      }
    );
  }
  handleSelectChange(event) {
    let value = event.target.value;
    let voice = this.state.voice;
    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    let createAnswer = this.state.createAnswer.slice(
      0,
      this.state.createAnswer.length
    );
    switch (value) {
      case "showanswer":
        for (let i = 0; i < final_text.length; i++) {
          final_text[i] = [];
          createAnswer[i] = this.state.checkAnswer[i];
        }
        break;
      case "usmale":
        voice = "US English Male";
        break;
      case "usfemale":
        voice = "US English Female";
        break;
      case "ukmale":
        voice = "UK English Male";
        break;
      case "ukfemale":
        voice = "UK English Female";
        break;
      default:
        break;
    }
    this.setState({
      value: value,
      final_text: final_text,
      createAnswer: createAnswer,
      voice: voice,
    });
  }

  showAnswers(key) {
    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    let createAnswer = this.state.createAnswer.slice(
      0,
      this.state.createAnswer.length
    );

    final_text[key] = [];
    createAnswer[key] = this.state.checkAnswer[key];
    this.setState((str) => ({
      final_text: final_text,
      createAnswer: createAnswer,
    }));
  }
  handleSubmit(evt) {
    evt.preventDefault();
    let english = this.state.english;
    let vietnames = this.state.vietnames;
    const en_length = this.state.english.length;
    const vi_length = this.state.vietnames.length;
    if (en_length > 0 && vi_length > 0) {
      if (this.state.english[en_length - 1] !== ".") {
        english += ".";
        console.log("ok");
      }
      if (this.state.vietnames[vi_length - 1] !== ".") {
        vietnames += ".";
        console.log("oker");
      }
      let final_text = HandleText(english.split("."));
      const checkAnswer = HandleText(english.split("."));
      let createAnswer = [];
      let storeIndex = [];
      let show = [];
      let inputArr = [];
      for (let i = 0; i < checkAnswer.length; i++) {
        let arr = [];
        inputArr.push(true);
        storeIndex.push(0);
        show.push(0);
        for (let j = 0; j < checkAnswer[i].length; j++) {
          arr.push("___");
        }
        createAnswer.push(arr);
      }
      final_text.forEach((element) => {
        return Shuffle(element);
      });
      let sentences_english = english.split(".");
      let sentences_vietnames = vietnames.split(".");
      console.log(sentences_english.length, sentences_vietnames.length);

      this.setState({
        english_text: sentences_english,
        vietnames_text: sentences_vietnames,
        final_text: final_text,
        checkAnswer: checkAnswer,
        createAnswer: createAnswer,
        storeIndex: storeIndex,
        submit: false,
        inputArr: inputArr,
      });
    }
  }
  render() {
    let gameStart = this.generateButtons();
    // console.log(this.state.english_text);
    // console.log(this.state.guessWord);
    //console.log(this.state.key);
    // console.log(this.state.index);
    // console.log(this.state.storeIndex);
    // console.log(this.state.createAnswer);
    //console.clear();
    if (this.state.guessWord === this.state.english_text[this.state.key]) {
      console.log("Your answer is correct");
    }
    return (
      <div className="App">
        {this.state.submit ? (
          <form onSubmit={this.handleSubmit} id="form">
            <label>
              <input
                type="text"
                name="english"
                placeholder="English"
                value={this.state.english}
                onChange={this.handleChange}
              />
            </label>
            <label>
              <input
                type="text"
                name="vietnames"
                placeholder="Vietnamese"
                value={this.state.vietnames}
                onChange={this.handleChange}
              />
            </label>
            <button id="submit">Submit!</button>
          </form>
        ) : (
          <div>
            {/* <button onClick={this.showAnswers} className="Button">
              Show answer
            </button> */}
            <section id="meo">
              <form onSubmit={this.showAnswers}>
                <label>
                  <div className="select">
                    <select
                      name="slct"
                      id="slct"
                      value={this.state.value}
                      onChange={this.handleSelectChange}
                    >
                      <option value="default">Choose an option</option>
                      <option value="showanswer">Show Answers</option>
                      <option value="usmale">US English Male Voice</option>
                      <option value="usfemale">US English FeMale Voice</option>
                      <option value="ukmale">UK English Male Voice</option>
                      <option value="ukfemale">UK English FeMale Voice</option>

                      {/* <option value="2">No JS</option>
                      <option value="3">Nice!</option> */}
                    </select>
                  </div>
                </label>
              </form>
            </section>
            <p className="Hangman-btns">{gameStart}</p>
          </div>
        )}
      </div>
    );
  }
}

export default App;
