function HandleText(english) {
  let list_english = [];
  for (const element of english) {
    list_english.push(element);
  }

  let final_english = [];
  for (const element of list_english) {
    final_english.push(element.trim().split(" "));
  }
  if (final_english.length > 0) {
    final_english.pop();
  }
  return final_english;
}
export default HandleText;
